import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";

const fetchBinary = (index, callback) => {
  fetch('/data.json')
  .then(res => res.json())
  .then((data) => {
    callback(data?.binaries[index]);
  })
  .catch(err => {
    alert(err);
  })
}

const Binary = () => {
  const {id} = useParams();
  const [binary, setBinary] = useState({});

  useEffect(() => {
    fetchBinary(id, setBinary);
  }, []);

 return (
   <>
     <div className='pagetitle'>
       <h1>{binary?.name}</h1>
       <nav>
         <ol className='breadcrumb'>
           <li className='breadcrumb-item'>
             <a href='index.html'>Home</a>
           </li>
           <li className='breadcrumb-item'>Binaries</li>
           <li className='breadcrumb-item active'>{binary?.name}</li>
         </ol>
       </nav>
     </div>
     {/* <!-- End Page Title --> */}

     <section className='section'>
       <div className='row'>
         <div className='col-lg-10'>
           <div className='card'>
             <div className='card-header d-flex justify-content-between'>
               <Link type='button' to='/' className='btn btn-link p-2'>
                 <i className='bi bi-arrow-left-circle text-black-50 h5'></i>
               </Link>

               <div
                 className='btn-group'
                 role='group'
                 aria-label='Basic mixed styles example'
               >
                 <Link
                   type='button'
                   href={binary?.repository}
                   target='_blank'
                   className='btn btn-dark btn-sm p-2'
                 >
                   <i className='bi bi-github me-1'></i> GitHub Repository
                 </Link>
                 <esp-web-install-button
                   class='btn btn-info p-0'
                   id='installButton'
                   manifest={binary?.manifest}
                 >
                   <button
                     type='button'
                     className='btn btn-info text-white'
                     slot='activate'
                   >
                     <i className='bi bi-upload me-1'></i> Flash
                   </button>
                   <span slot='unsupported'>
                     Ah snap, your browser doesn't work!
                   </span>
                   <span slot='not-allowed'>
                     Ah snap, you are not allowed to use this on HTTP!
                   </span>
                 </esp-web-install-button>
               </div>
             </div>
             <div className='card-body pt-1'>
               <p>{binary?.description}</p>
             </div>
           </div>
         </div>
       </div>
     </section>
   </>
 );
};

export default Binary;